<template>
    <div id="app">
        <el-container :class="{'mobile': isMobile, 'menu-open': navOpen}">
            <el-aside :width="navWidth">
                <el-row>
                    <el-col>
                        <el-menu default-active="0">
                            <el-menu-item index="0" @click="filterProductByCategory(0)">
                                <i class="el-icon-caret-right"></i>
                                <span slot="title">全部商品</span>
                            </el-menu-item>
                            <el-menu-item
                                    v-for="item in categorySource"
                                    :index="item.gsx$index.$t"
                                    :key="item.gsx$index.$t" @click="filterProductByCategory(parseInt(item.gsx$index.$t))">
                                <i class="el-icon-caret-right"></i>
                                <span slot="title">{{ item.gsx$title.$t }}</span>
                            </el-menu-item>
                        </el-menu>
                    </el-col>
                </el-row>
            </el-aside>
            <el-container id="main-container">
                <el-header>
                    <el-row type="flex" align="middle">
                        <el-col :xs="3" :md="1"><span class="menu-button" @click="toggleNav" v-if="!isMobile"><i class="el-icon-arrow-right"></i></span></el-col>
                        <el-col :xs="18" :md="22"><div class="grid-content bg-purple-dark">好棉國際股份有限公司</div></el-col>
                        <el-col :xs="3" :md="1"><span class="menu-button" @click="toggleNav" v-if="isMobile"><i class="el-icon-more"></i></span></el-col>
                    </el-row>
                </el-header>
                <el-main>
                    <el-row :gutter="15">
                        <el-col :xs="24" :md="8" :lg="6" v-for="(item, index) in source" :key="index" v-if="categoryFilter === 0 || parseInt(item.gsx$category.$t) === categoryFilter">
                            <el-card :body-style="{ padding: '0px' }">
                                <img :src="item.gsx$photo.$t.split(',')[0]" class="image">
                                <div style="padding: 14px;">
                                    <h3>{{ item.gsx$title.$t }}</h3>
                                    <div class="bottom clearfix">
                                        <time class="time">{{ item.gsx$sku.$t }}</time>
                                        <el-button type="text" class="button" :data-id="index" @click="showDetail">詳細內容</el-button>
                                    </div>
                                </div>
                            </el-card>
                        </el-col>
                    </el-row>

                    <el-dialog
                            title="產品詳情"
                            :visible.sync="dialogVisible"
                            width="720px"
                            top="80px"
                            :center="true">
                        <el-row :gutter="15">
                            <el-col :xs="24" :md="12">
                                <el-carousel :autoplay="false" :height="productPhotoHeight" arrow="never" indicator-position="none" ref="productCarousel">
                                    <el-carousel-item v-for="photo in productPhoto" :key="photo">
                                        <img :src="photo" />
                                    </el-carousel-item>
                                </el-carousel>
                                <ul class="thumbnails" v-if="productPhoto.length > 1" ref="productThumbnail">
                                    <li v-for="(photo, index) in productPhoto" :key="photo" :class="index === 0 ? 'active' : ''">
                                        <img :src="photo" :style="productThumbnailStyle" @click="setActivePhoto(index)" />
                                    </li>
                                </ul>
                            </el-col>
                            <el-col :xs="24" :md="12">
                                <h3 id="product-title">{{ productTitle }}</h3>
                                <ul id="product-feature" v-if="productFeature.length > 0">
                                    <li v-for="item in productFeature" :key="item">{{ item }}</li>
                                </ul>
                            </el-col>
                        </el-row>
                        <el-row :gutter="15">
                            <el-col :xs="24" :md="24">
                                <div id="product-detail" v-html="productDetail"></div>
                            </el-col>
                        </el-row>
                    </el-dialog>
                </el-main>
                <el-footer>{{ currentYear }} &copy; 好棉國際股份有限公司 All rights received.</el-footer>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'app',
    data: function() {
      return {
          isMobile: false,
          navOpen: false,
          navWidth: '300px',
          source: [],
          selectId: null,
          currentYear: (new Date()).getFullYear(),
          dialogVisible: false,
          productTitle: '商品名稱',
          productPhoto: [],
          productFeature: [],
          productDetail: '商品說明',
          productPhotoHeight: '320px',
          productThumbnailStyle: 'width: 60px',
          categorySource: [],
          categoryFilter: 0
      };
    },
    created: function () {
        let thisComp = this;
        let photoHeight = (window.innerWidth * 0.95 - 50);
        thisComp.navOpen = window.innerWidth > 800;
        thisComp.navWidth = window.innerWidth > 800 ? '240px' : '200px';
        thisComp.isMobile = window.innerWidth <= 800;
        thisComp.productPhotoHeight = (photoHeight > 320 ? 320 : photoHeight) + 'px';
        thisComp.productThumbnailStyle = 'width: ' + (((photoHeight > 320 ? 320 : photoHeight) - 20) / 5) + 'px';
        // https://spreadsheets.google.com/feeds/list/1yWTtjzFKDaylv-EOAJyKgUp70Srq6oi8ZpotiZ00SlQ/1/public/values?alt=json
        // ./products.json
        axios.get('./products.json')
            .then(function (response) {
                thisComp.source = response.data.feed.entry;
            });
        // https://spreadsheets.google.com/feeds/list/1yWTtjzFKDaylv-EOAJyKgUp70Srq6oi8ZpotiZ00SlQ/2/public/values?alt=json
        // ./categories.json
        axios.get('./categories.json')
            .then(function (response) {
                thisComp.categorySource = response.data.feed.entry;
            });
    },
    methods: {
        toggleNav: function () {
            this.navOpen = !this.navOpen;
        },
        showDetail: function (event) {
            if (this.isMobile) {
                this.navOpen = false;
            }
            this.dialogVisible = true;
            let targetId = event.target.tagName === 'SPAN' ? event.target.parentElement.getAttribute('data-id') : event.target.getAttribute('data-id');
            let productSource = this.source[targetId];
            if (typeof productSource !== 'undefined') {
                this.productTitle   = productSource.gsx$title.$t;
                this.productPhoto   = productSource.gsx$photo.$t.split(',');
                this.productFeature = productSource.gsx$feature.$t === '' ? [] : productSource.gsx$feature.$t.split(',');
                this.productDetail  = productSource.gsx$content.$t;
                this.setActivePhoto(0);
            }
            setTimeout(function() { document.getElementsByClassName('el-dialog__wrapper').item(0).scrollTo(0, 0);}, 300);
        },
        setActivePhoto: function (index) {
            if (typeof this.$refs.productCarousel !== 'undefined') {
                this.$refs.productCarousel.setActiveItem(index);
            }

            if (typeof this.$refs.productThumbnail !== 'undefined') {
                let liSet = this.$refs.productThumbnail.getElementsByTagName('li');
                for (let i = 0; i < liSet.length; i++) {
                    liSet[i].classList.remove('active');
                }
                this.$refs.productThumbnail.getElementsByTagName('li')[index].setAttribute('class', 'active');
            }
        },
        filterProductByCategory: function(index) {
            this.categoryFilter = index;
            if (this.isMobile) {
                this.navOpen = false;
            }
        }
    }
}
</script>

